
.z-multi-table-select {
  .el-input__inner {
    cursor: pointer;
  }
  .el-input__icon.el-icon-arrow-down {
    transition: transform 0.3s;
  }
  &.is-clear .el-input__icon.el-icon-arrow-down {
    display: none;
  }
  &.is-open .el-input__icon.el-icon-arrow-down {
    transform: rotateZ(-180deg);
  }
}
